<template>
  <div class="level la is-mobile">
    <div class="level-left ca">
      <span
        v-if="loading"
        class="loader"
        style="width: 30px; height: 30px;"
      />
      <label
        v-else
        class="switch"
      >
        <input
          :id="'switch_component_' + id"
          :value="modelValue"
          :class="{'checked' : modelValue, 'disabled' : disabled}"
          :disabled="disabled"
          type="checkbox"
          @change="this.$emit('update:modelValue', !this.modelValue);"
        >
        <span class="slider round" />
        
      </label>
      <label
        v-if="text && !loading"
        class="ml-3 is-unselectable is-clickable"
        :for="'switch_component_' + id"
      >{{ text }}</label>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Switch",
    props : ["modelValue", "text", "disabled", "loading"],
    data () {
      return {
        id: this.$store.getters.getUidSeq
      }
    }
  };
</script>

<style lang="scss">
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 16px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    margin-left: 6px;
    cursor: pointer;
    top: 2px;
    left: 0;
    right: 0;
    bottom: -2px;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 10px;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 24px;
    width: 24px;
    top: -4px;
    left: -4px;
    background-color: white;
    -webkit-transition: .2s;
    transition: .2s;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
    border-radius: 100%;
  }

  input.disabled + .slider {
    background-color: lighten(#ccc, 20%);
    cursor: not-allowed;
  }

  input.checked + .slider {
    background-color: #3273dc;
  }
  input.checked.disabled + .slider {
    background-color: lighten(#3273dc, 20%);
    cursor: not-allowed;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #3273dc;
  }

  input:focus.disabled  + .slider {
    background-color: lighten(#3273dc, 20%);
    cursor: not-allowed;
  }

  input.checked + .slider:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
  }
</style>