<template>
  <div
    v-if="forms !== undefined && categories !== undefined"
    id="responses"
    class="box"
    style="overflow-x: auto;"
  >
    <div
      class="modal"
      :class="{'is-active' : selected_form !== undefined}"
    >
      <div
        class="modal-background"
        @click="close"
      />
      <div
        class="modal-card"
        style="min-width: 70%"
      >
        <header class="modal-card-head">
          <p class="modal-card-title">
            {{ selected_form?.title }}
          </p>
          <button
            class="delete"
            aria-label="close"
            @click="close"
          />
        </header>
        <section
          :key="selected_form"
          class="modal-card-body"
        >
          <div
            v-if="!selected_form_questions"
            class="flex column ca vca"
          >
            <span class="subtitle">A carregar formulário</span>
            <span
              class="loader mt-3"
              style="width: 50px; height: 50px;"
            />
          </div>
          <div
            v-for="(section, sectionIndex) in selected_form_questions.form.sections"
            v-else
            :key="section.id"
            class="flex column vca"
          >
            <span class="title">
              <template v-if="!selected_form_questions.form.hide_section_numbers">{{ sectionIndex+1 }}. </template>{{ section.section_name }}
            </span>
            <div
              v-for="(question, questionIndex) in section.questions"
              :key="question.id"
              style="text-align: center; width: 100%;"
            >
              <hr style="height: 0.5px;">
              <span
                class="subtitle mb-1"
                :class="{'required' : question.required}"
              >
                <template v-if="!selected_form_questions.form.hide_question_numbers">{{ questionIndex+1 }}. </template>{{ question.question }}</span> <br>
              <span>({{ getFieldTypeTitle(question.properties.field_type) }})</span>
              <div
                v-if="question.properties.field_type === 'text_exposition'"
                class="box content"
                v-html="question.properties.content"
              />
              <div
                v-else-if="question.properties.field_type === 'selectboxes'"
                class="box"
                style="text-align: left;"
              >
                <ul
                  class="ml-4 mr-2"
                  style="list-style-type: square;"
                >
                  <li
                    v-for="option in question.properties.options"
                    :key="option.id"
                  >
                    {{ option.question }}
                  </li>
                </ul>
                <div
                  v-if="question.properties.min_selected || question.properties.max_selected"
                  style="text-align: center;"
                >
                  <hr>
                  <span class="is-size-4">Restrições</span><br>
                  <span v-if="question.properties.min_selected && question.properties.min_selected === question.properties.options.length">
                    Deve selecionar todas as opções<br>
                  </span>
                  <span v-else-if="question.properties.min_selected && question.properties.min_selected === question.properties.max_selected">
                    Deve selecionar {{ question.properties.max_selected }} {{ question.properties.max_selected == 1 ? 'opção' : 'opções' }}
                  </span>
                  <span v-else>
                    <span v-if="question.properties.min_selected">
                      Deve selecionar pelo menos {{ question.properties.max_selected }} {{ question.properties.min_selected == 1 ? 'opção' : 'opções' }}<br>
                    </span>
                    <span v-if="question.properties.max_selected">
                      Deve selecionar até {{ question.properties.max_selected }} {{ question.properties.max_selected == 1 ? 'opção' : 'opções' }}<br>
                    </span>
                  </span>
                </div>
              </div>
              <div 
                v-else-if="question.properties.field_type === 'multiplechoice'" 
                class="box" 
                style="text-align: left;"
              >
                <ul
                  class="ml-4 mr-2"
                  style="list-style-type: square;"
                >
                  <li
                    v-for="option in question.properties.options"
                    :key="option.id"
                  >
                    {{ option.question }}
                  </li>
                </ul>
              </div>
            </div>
            <hr style="width: 100%;">
          </div>
        </section>
      </div>
    </div>
    <table class="table is-striped is-hoverable is-fullwidth">
      <caption class="title is-scrollable">
        Formulários da Plataforma
      </caption>
      <thead>
        <tr>
          <th colspan="2">
            <a
              href="createForm"
              class="button is-primary mr-4 mb-1"
            >Criar novo formulário</a>
          </th>
          <th colspan="2" />
          <th colspan="6">
            <div class="control has-icons-left">
              <span class="icon"><i class="fas fa-search" /></span>
              <Input
                v-model="search"
                placeholder="Pesquisar"
                class="input is-expanded"
                type="text"
                @input="sort()"
              />
            </div>
          </th>
        </tr>
        <tr>
          <th>
            Formulário
          </th>
          <th
            align="center"
            colspan="2"
          >
            Respostas
          </th>
          <th align="center">
            Tipo
          </th>
          <th align="center">
            Categoria
          </th>
          <th align="center">
            Ativo
          </th>
          <th
            colspan="4"
            align="center"
          >
            Controlos
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="form in filtered_forms"
          :key="form.id"
          :class="{'form-hidden' : form.hidden}"
        >
          <td>
            <a @click="selected_form = form; update_selected_form() ">
              {{ form.title }}
            </a>
          </td>
          <td
            align="right"
            class="subtitle"
          >
            <a
              v-if="form.responses > 0"
              class=" flex row vca ra
              "
              :href="'responses/'+form.id"
            >
              {{ form.responses }}
            </a>
            <span v-else>
              {{ form.responses }}
            </span>
          </td>
          <td align="left">
            <a
              v-if="form.responses > 0"
              :href="'responses/'+form.id"
            >
              <span
                class="icon flex row vca"
                style="font-size: 1.2em;"
              >
                <i class="fas fa-external-link-alt" />
              </span>
            </a>
          </td>
          <td align="right">
            <div class="select is-rounded is-fullwidth">
              <select
                v-model.number="form.type"
                style="color : black;"
                :disabled="form.signup_uses > 0 || form.feedback_uses > 0 || form.responses > 0 || (form.category && categories[form.category].formproposalid == form.id)"
                @change="changeType(form.id, form.type == '' ? null : form.type)"
              >
                <option
                  v-for="(type, key) in types"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ type.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="right">
            <div
              v-if="form.type==2"
              class="select is-rounded is-fullwidth"
            >
              <select
                v-model.number="form.category"
                style="color : black;"
                :disabled="form.responses > 0 || (form.category && categories[form.category].formproposalid == form.id)"
                @change="changeCategory(form.id, form.category == '' ? null : form.category)"
              >
                <option disabled />
                <option
                  v-for="(category, key) in categories"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ category.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="center">
            <Switch 
              v-if="form.category && form.type==2" 
              :model-value="form.category && categories[form.category].formproposalid == form.id"
              :disabled="form.category && categories[form.category].formproposalid == form.id"
              :loading="form.loading"
              class="ca"
              @update:modelValue="form.loading=true; changeActive(form.category, form.id, form.type, form)"
            />
          </td>
          <td align="center">
            <a
              title="Duplicar"
              @click="duplicate(form.id)"
            >
              <i class="far fa-clone" />
            </a>
          </td>
          <td align="center">
            <a 
              :href="'createForm/' + form.id" 
              title="Editar"
            >
              <i class="fas fa-edit" />
            </a>
          </td>
          <td align="center">
            <a
              v-if="form.responses == 0 && !(form.category && categories[form.category].formproposalid == form.id) && form.signup_uses == 0 && form.feedback_uses == 0"
              title="Remover"
              aria-label="delete"
              @click="remove(form.id)"
            >
              <i class="fas fa-trash-alt" />
            </a>           
          </td>
          <td align="center">
            <a
              v-if="form.hidden"
              title="Tornar visível"
              @click="hide(form)"
            >
              <i class="fas fa-eye" />
            </a>
            <a
              v-else
              title="Esconder"
              @click="hide(form)"
            >
              <i class="fas fa-eye-slash" />
            </a>       
          </td>
        </tr>
        <tr>
          <td
            colspan="10"
            class="has-background-info-light p-0"
          >
            <a
              class="p-3 m-0 is-primary is-light flex row ca width-100"
              @click="toggleHiddenForms()"
            >{{ showHidden ? 'Esconder' : 'Ver' }} formulários escondidos</a>
          </td>
        </tr>
      </tbody>
    </table>
    <div 
      v-if="notification"  
      class="flex row ca snackbar ontop bottom"
    >
      <Notification
        bottom
        class="is-success"
        duration="4000" 
        :message="notification" 
        @deleted="notification=undefined"
      />
    </div>
  </div>
</template>

<script>
import Switch from "@/components/Switch"
import { API } from "@/http-common";
import {stringIn, getCookie, getFieldTypeTitle} from "@/common"
import Input from "@/components/Input"
import Notification from "@/components/Notification"
export default {
  name: "CreateResponse",
  components : {Switch, Input, Notification},
  data() {
    return {
      forms: undefined,
      categories : undefined,
      types : undefined,
      search : "",
      notification : undefined,
      showHidden : false,
      fetchedHidden : false,
      selected_form : undefined,
      selected_form_questions : undefined
    };
  },
  created() {
    document.title = "Formulários"
    Promise.all([
      API.get("formType/")
        .then((response) => this.types = response.data),
      API.get("form/")
        .then((response) => {this.forms = response.data; this.sort()}),
      API.get("eventType/")
        .then((response) => this.categories = response.data)])
      .then(() => {this.loading = false})
  },
  methods: {
    toggleHiddenForms() {
      if (!this.showHidden && !this.fetchedHidden)
        API.get("form/?hidden=true").then((response) => {this.forms = response.data; this.sort(); this.fetchedHidden = true});
      this.showHidden = !this.showHidden;
      this.sort();
    },
    async hide(form) {
      if(confirm('Deseja alterar a visibilidade deste formulário?')) {
        const options = {
          method: "patch",
          url: "/form/" + form.id + "/"+"?hidden=true",
          headers: {
            "X-CSRFToken": getCookie("csrftoken"),
          },
          data : {
            hidden : !form.hidden
          }
        };
        await API(options);
        form.hidden= !form.hidden;
        this.notification = "A visibilidade do formulário foi alterada"
      }
    },
    async remove(id) {
      this.notification = undefined;
      if(confirm('Deseja eliminar este formulário?')) {
        this.forms = this.forms.filter((x) => (x.id != id));
        const options = {
          method: "delete",
          url: "/form/" + id + "/",
          headers: {
            "X-CSRFToken": getCookie("csrftoken"),
          },
        };
        await API(options);
        this.notification = "Formulário removido com sucesso"
      }
    },
    async duplicate(id) {
      this.notification = undefined;
      if(confirm('Deseja criar uma cópia deste formulário?')) {
        var original;
        await API.get("form/".concat(id)).then((response) => original = response.data);
        original.id = undefined;
        original.title = original.title.concat(" (Cópia)")
        original.created_at = undefined;
        original.updated_at = undefined;
        original.creator = undefined;
        original.editable = undefined;
        original.responses = undefined;
        const options = {
          method: "post",
          url: "/form/",
          headers: {
            "X-CSRFToken": getCookie("csrftoken"),
          },
          data : original,
          transformResponse: [
            (data) => {
              const result = JSON.parse(data);
              result.responses = 0;
              result.editable = true;
              this.forms.push(result);
              this.notification = "Formulário duplicado com sucesso"
              return data;
            },
          ],
        };
        API(options);
      }
    },
    changeCategory(answer, category) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {
          "category": category
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeType(answer, type) {
      const options = {
        method: "patch",
        url: "/form/" + answer + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {
          "type": type
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    changeActive(category, active, type, form) {
      var string = "Deseja ativar este formulário?";
      if (this.categories[category].formproposalid) {
        string = string.concat(" Já existe um formulário ativo para esta categoria.");
      }
      if (confirm(string)) {
        var dataSend;
        if (type == 1)
          dataSend = {"formsignupid" : active};
        else if (type == 2)
          dataSend = {"formproposalid" : active};
        else if (type == 3)
          dataSend = {"formfeedbackid" : active};
        const options = {
          method: "patch",
          url: "/eventType/" + category + "/",
          headers: {
            "X-CSRFToken": getCookie("csrftoken"),
          },
          data: dataSend,
          transformResponse: [
            (data) => {
              this.response = data;
              form.loading = undefined;
              return data;
            },
          ],
        };
        API(options).then(() => {API.get("eventType/")
        .then((response) => this.categories = response.data);});
      }
      else
        form.loading = undefined;
    },
    sort() {
      this.forms.sort(
        (a, b) => {
          var result = (a.hidden ? 1 : 0) - (b.hidden ? 1 : 0);
          if (result == 0)
            result = a.type - b.type;
            if (result == 0) {
              result = ('' + a.title).localeCompare(b.title);
            }
            return result;
          }
        )
    },
    async update_selected_form() {
      if (this.selected_form) {
        await API.get("form/".concat(this.selected_form.id))
        .then((response) => 
          this.selected_form_questions = response.data
        )
      }
      return undefined;
    },
    close() {
      this.selected_form = undefined; this.selected_form_questions = undefined;
    },
    getFieldTypeTitle,
  },
  computed: {
    filtered_forms() {
      const result = this.forms.filter((x) => stringIn(x.title, this.search))
      if (!this.showHidden && this.fetchedHidden)
        return result.filter((x) => !x.hidden);
      return result;
    }
  }
};
</script>

<style scoped lang="scss">
  .noafter:after {
    content: '' !important; 
  }

  th, td, a {
    vertical-align: middle !important;
  }

  .form-hidden>* {
    background-color: rgba(0, 0, 0, 0.075);
    filter: brightness(93%);
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>